import { IMsg, IGetVersionMsg, IGetVersionResponse } from "@stechquick/algae/lib/protocols/fileSystem";
export interface IFileSystemItem {
    name: string;
    type: string;
}
export interface IFileInfo {
    name: string;
    ext?: string;
    type: "folder" | "file" | "base64convertedfile";
}
export interface IFileSystemItems {
    curPath: string
    files?: Array<IFileInfo>;
}
export interface IFileContent extends IFileInfo {
    content: string
}


export interface IWatchFileMsgRequest extends IMsg {
    type: "watch_files";
    files: Array<string>;
    /**
     * as miliseconds
     */
    interval: number;
}
export interface IWatchFilesMsgResponse extends IMsg {
    type: "watch_files_resp";
    statuses: Array<boolean>;
}
export interface IFileChangeMsgRequest extends IMsg {
    type: "file_change";
    files: Array<{
        file: string;
        event: "rename" | "change" | "delete";
    }>
}
export interface IListFilesMsg extends IMsg {
    path?: string;
    type: "list_files";
}
export interface IListFilesMsgResponse extends IMsg, IFileSystemItems {
    type: "list_files_resp";
}
export interface ISaveFilesMsg extends IMsg {
    type: "save_files";
    files: Array<IFileContent>;
    recursive?: boolean;
}
export interface ISaveFilesMsgResponse extends IMsg {
    type: "save_files_resp";
    result: boolean;
    filePaths?: Array<string>
}
export interface ICreateFolderMsg extends IMsg {
    type: "create_folder";
    folderName: string;
}
export interface ICreateFolderMsgResponse extends IMsg {
    type: "create_folder_resp";
}
export interface IDeleteFolderMsg extends IMsg {
    type: "delete_folder";
    folderName: string;
}
export interface IDeleteFolderMsgResponse extends IMsg {
    type: "delete_folder_resp";
}
export interface IDeleteFilesMsg extends IMsg {
    type: "delete_files";
    files: Array<string>;
}
export interface IDeleteFilesMsgResponse extends IMsg {
    type: "delete_files_resp";
    result: boolean;
}
export interface IGetFilesMsg extends IMsg {
    type: "get_files";
    files: Array<string>;
}
export interface IGetFilesMsgResponse extends IMsg {
    type: "get_files_resp";
    files: Array<IFileContent>;
}

export type RequestOptions
    = IListFilesMsg | ISaveFilesMsg | ICreateFolderMsg | IDeleteFolderMsg | IDeleteFilesMsg | IGetFilesMsg | IWatchFileMsgRequest | IFileChangeMsgRequest
    | IGetVersionMsg;

export type ResponseOptions
    = IListFilesMsgResponse | ISaveFilesMsgResponse | ICreateFolderMsgResponse | IDeleteFolderMsgResponse | IDeleteFilesMsgResponse | IGetFilesMsgResponse | IWatchFilesMsgResponse
    | IGetVersionResponse;

export interface INetworkMessage {
    id: string;
    reply?: string;
}
export interface INetworkMessageRequest extends INetworkMessage {
    msg: RequestOptions;
}
export interface INetworkMessageResponse extends INetworkMessage {
    msg: ResponseOptions;
}